<h1 id="电子发票" tabindex="-1"><a class="header-anchor" href="#电子发票" aria-hidden="true">#</a> 电子发票</h1>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'corp_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxxxxxxxxx'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'secret'</span>   <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxx'</span><span class="token punctuation">,</span>
    <span class="token comment">//...</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token class-name static-context">Factory</span><span class="token operator">::</span><span class="token function">work</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h2 id="查询电子发票" tabindex="-1"><a class="header-anchor" href="#查询电子发票" aria-hidden="true">#</a> 查询电子发票</h2>
<p>https://work.weixin.qq.com/api/doc#11631</p>
<p>API:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword type-declaration">mixed</span> <span class="token function">get</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$encryptCode</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">invoice</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'CARDID'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'ENCRYPTCODE'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="批量查询电子发票" tabindex="-1"><a class="header-anchor" href="#批量查询电子发票" aria-hidden="true">#</a> 批量查询电子发票</h2>
<p>https://work.weixin.qq.com/api/doc#11974</p>
<p>API:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword type-declaration">mixed</span> <span class="token function">select</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$invoices</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<p>{info} $invoices: 发票参数列表</p>
</blockquote>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$invoices</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token punctuation">[</span><span class="token string double-quoted-string">"card_id"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"CARDID1"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"encrypt_code"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"ENCRYPTCODE1"</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">[</span><span class="token string double-quoted-string">"card_id"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"CARDID2"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"encrypt_code"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"ENCRYPTCODE2"</span><span class="token punctuation">]</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">invoice</span><span class="token operator">-></span><span class="token function">select</span><span class="token punctuation">(</span><span class="token variable">$invoices</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h2 id="更新发票状态" tabindex="-1"><a class="header-anchor" href="#更新发票状态" aria-hidden="true">#</a> 更新发票状态</h2>
<p>https://work.weixin.qq.com/api/doc#11633</p>
<p>API:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword type-declaration">mixed</span> <span class="token function">update</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$encryptCode</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$status</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<p>{warning} $status: 发报销状态</p>
<blockquote>
<ul>
<li>INVOICE_REIMBURSE_INIT：发票初始状态，未锁定；</li>
<li>INVOICE_REIMBURSE_LOCK：发票已锁定，无法重复提交报销;</li>
<li>INVOICE_REIMBURSE_CLOSURE:发票已核销，从用户卡包中移除</li>
</ul>
</blockquote>
</blockquote>
<h2 id="批量更新发票状态" tabindex="-1"><a class="header-anchor" href="#批量更新发票状态" aria-hidden="true">#</a> 批量更新发票状态</h2>
<p>https://work.weixin.qq.com/api/doc#11633</p>
<p>API:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword type-declaration">mixed</span> <span class="token function">batchUpdate</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$invoices</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$openid</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$status</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$invoices</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token punctuation">[</span><span class="token string double-quoted-string">"card_id"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"CARDID1"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"encrypt_code"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"ENCRYPTCODE1"</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">[</span><span class="token string double-quoted-string">"card_id"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"CARDID2"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"encrypt_code"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"ENCRYPTCODE2"</span><span class="token punctuation">]</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$openid</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'oV-gpwSU3xlMXbq0PqqRp1xHu9O4'</span><span class="token punctuation">;</span>

<span class="token variable">$status</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'INVOICE_REIMBURSE_CLOSURE'</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">invoice</span><span class="token operator">-></span><span class="token function">batchUpdate</span><span class="token punctuation">(</span><span class="token variable">$invoices</span><span class="token punctuation">,</span> <span class="token variable">$openid</span><span class="token punctuation">,</span> <span class="token variable">$status</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div>